import React,{Component} from 'react';
import TreeView from '@material-ui/lab/TreeView';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import TreeItem from '@material-ui/lab/TreeItem';
import Checkbox from '@material-ui/core/Checkbox';
import Typography from '@material-ui/core/Typography';


export default class SelectTreeView extends Component{
    constructor(props) {
        super(props);
        this.state = { 
          checked: [],
          expanded: [],
          loading:true,
          AllStu:[],
          treeSet:this.props.tree,
         };
    };
   
  createOrgStructureLevel = (treeSet,multi) => {
    let elements = [];
    let {checked} = this.state
     treeSet.forEach((v, k) => {
       const { value, label,children} = v;
       const index = checked.findIndex(x=>x===value)
       const exist = index !==-1?true:false

       if (k.length !== 0) {
         const comp = (
           <div style={{ display: 'flex', alignItems: 'center' }}>
             <Checkbox
               value={value}
               checked={exist}
               onChange={() => this.checkBoxClicked(value,index,children)}
               color="primary"
             />
              <Typography variant="caption">{label}</Typography>
           </div>
         );

         elements.push(
           children && children.length > 0 ? (
             <TreeItem key={value} nodeId={value} label={comp}>
               {this.createOrgStructureLevel(children,exist)}
             </TreeItem>
           ) : (
             <TreeItem key={value} nodeId={value} label={comp} />
           ),
         );
       } else if (children) {
         elements.push(this.createOrgStructureLevel(children,exist));
       }
     });

     return elements;
   };


temppush =(value,checked,children)=>{
  checked.push(value)
  if(children && children.length > 0){
    children.forEach(x=>{
      checked = this.temppush(x.value,checked,x.children)
    })
  }  
  return checked;
}


tempslice =(value,checked,children)=>{

    let _checked = checked.filter(t=>t!==value)
    if(children && children.length > 0){
      children.forEach(x=>{
        _checked = this.tempslice(x.value,_checked,x.children)
      })
    }
    return _checked;
  }


   checkBoxClicked = (value, index,children) => {
    let checked = this.state.checked
    
    if(index!==-1){//존재한다면
      checked= this.tempslice(value,checked,children)
     }else{
      checked =this.temppush(value,checked,children)
     }
     console.log(checked)
     this.props.passChildData(checked)
     this.setState({checked:checked})
 };


render(){
  const {treeSet} = this.state
    return (
        <div>
             <div >
             <TreeView
              defaultCollapseIcon={<ExpandMoreIcon />}
              defaultExpandIcon={<ChevronRightIcon />}>
              {treeSet.length !== 0 ? this.createOrgStructureLevel(treeSet,false) : null}
            </TreeView>
            </div>
        </div>
      );
}
}